import React from 'react'

export default ({onClose, onSubmit, header, form}) => (
   <div className="modal">
      <button onClick={onClose} className='close'>×</button>
      <form>
         <h2>{header}:</h2>
         {form}
         <button onClick={e => {onSubmit(e); onClose()}} type='button' className='submit'>Продолжить</button>
      </form>
   </div>
)