import React, { useState, useEffect } from 'react'
import debounce from 'lodash.debounce'
import {colors} from '../config'

export default ({onChange, top, bottom, openGradientModal, gradientDirection}) => {
   const [textArray, setTextArray] = useState([])
   const [colorInputs, setColorInputs] = useState(['', ''])

   useEffect(() => {
      setTextArray(getTextArray(gradientDirection))
   }, [top, bottom, gradientDirection])

   const onDrag = (type, e, change) => {
      const {y} = document.getElementById('colors').getBoundingClientRect()
      let newTop
      let newBottom
      if (type === 'top') {
         newTop = Math.min(Math.round((e.pageY - y) / 50), bottom - 1)
         onChange(newTop < 0 ? 0 : newTop, bottom, change)
      } else {
         newBottom = Math.max(Math.round((e.pageY - y) / 50), top + 1)
         onChange(top, newBottom > colors.length - 1 ? colors.length : newBottom, change)
      }
   }

   const getTextArray = (gradientDirection) => {
      let arr = []
      for (let i = 0; i < bottom; i++) {
         if (i < top) {
            arr.push('')
            continue
         }
         if (gradientDirection === 'center') {
            if (i - top === 0) arr.push(20000)
            else arr.push(20000 - (i - top) * Math.floor((20000 / (bottom - top - 1))))
         }
         else {
            if (i - top === 0) arr.push(-200)
            else arr.push(-200 + (i - top) * Math.floor((400 / (bottom - top - 1))))
         }
      }
      return arr
   }

   const onInputChange = debounce((value, index) => {
      const closest = textArray.reduce((a, b) => {
         return Math.abs(b - value) < Math.abs(a - value) ? b : a;
      });
      
      if (index === 0) onChange(textArray.indexOf(closest), bottom, true)
      else onChange(top, textArray.indexOf(closest), true)
   }, 2000)

   const resetColors = () => {
      setColorInputs(['', ''])
      onChange(0, 10, true)
   }

   return (
      <div className='colors' id='colors'>
         <button onClick={openGradientModal}>Направление</button>
         <div id='colorsInputs'>
            <input
               type='number'
               value={colorInputs[0]}
               onChange={e => {
                  setColorInputs([e.target.value, colorInputs[1]])
                  onInputChange(e.target.value, 0)
               }}
            />
            <input
               type='number'
               value={colorInputs[1]}
               onChange={e => {
                  setColorInputs([colorInputs[0], e.target.value])
                  onInputChange(e.target.value, 1)
               }}
            />
            <button onClick={() => resetColors()}>x</button>
         </div>
         <div className="hr top" draggable='true' onDrag={e => onDrag('top', e)} onDragEnd={e => onDrag('top', e, true)} style={{top: top*50}}></div>
         {colors.map((item, index) => 
            <div className='color-item' style={{backgroundColor: item.color}} id={index} key={index}>
               <span>{textArray[index]}</span>
            </div>
         )}
         <div className="hr bottom" draggable='true' onDrag={e => onDrag('bottom', e)} onDragEnd={e => onDrag('bottom', e, true)}  style={{top: bottom*50}}></div>
      </div>
   )
}